@charset "utf-8";

#root {
    width: 20%;
    margin: 1rem auto;
}
.buttonbox {
    display: flex;
    flex-wrap: wrap;
}
button {
    width: 25%;
    height: 40px;
    background: #000;
    color: #fff;
    border: 1px solid #999;
}
input {
    width: 97%;
    height: 30px;
    outline: none;
    margin-bottom: 0.2rem;
}
.gg {
    width: 50%;
}

